import React from 'react';
import PageWrapper from '../components/PageWrapper';

const About = () => {
  return (
    <PageWrapper>
      <h1 className="text-3xl font-bold text-gray-900 mb-6 text-center">
        About AI Tools Hub
      </h1>
      <div className="bg-white p-6 rounded-md shadow-sm border border-gray-200">
        <p className="text-gray-700 mb-4">
          Welcome to <span className="font-semibold">AI Tools Hub</span> — a curated collection of powerful and practical AI tools designed to help you work smarter, create faster, and explore what's possible with modern tech.
        </p>
        <p className="text-gray-700 mb-4">
          Whether you're a developer, content creator, student, or just someone curious about AI, this platform is built to make it easy (and fun) to discover tools that fit your needs. From text generators to design assistants — it’s all here in one place.
        </p>
        <p className="text-gray-700">
          Built by <span className="font-semibold">Veeresh H P</span>, this hub is all about sharing useful tools, staying up to date with innovation, and helping people get more done with less effort. We’re always updating, so keep checking back for new finds!
        </p>
      </div>
    </PageWrapper>
  );
};

export default About;
