import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Header from './components/Header';
import Hero from './components/Hero';
import ToolsSection from './components/ToolsSection';
import Footer from './components/Footer';
import ComingSoonModal from './components/ComingSoonModal';
import Login from './components/Login';
import Signup from './components/Signup';
import About from './components/About';
import Contact from './components/Contact';
import ResetPassword from './components/ResetPassword';
import ThemeToggle from './components/ThemeToggle'; // 👈 Theme toggle component
import { ThemeProvider } from './contexts/ThemeContext';
import './index.css';

function App() {
  const [isDarkMode, setIsDarkMode] = useState(() => {
    return (
      localStorage.getItem('theme') === 'dark' ||
      (!localStorage.getItem('theme') &&
        window.matchMedia('(prefers-color-scheme: dark)').matches)
    );
  });

  useEffect(() => {
    if (isDarkMode) {
      document.documentElement.classList.add('dark');
      localStorage.setItem('theme', 'dark');
    } else {
      document.documentElement.classList.remove('dark');
      localStorage.setItem('theme', 'light');
    }
  }, [isDarkMode]);

  const [isModalOpen, setIsModalOpen] = useState(false);
  const openModal = () => setIsModalOpen(true);
  const closeModal = () => setIsModalOpen(false);

  return (
    <ThemeProvider value={{ isDarkMode, toggleDarkMode: () => setIsDarkMode(prev => !prev) }}>
      <Router>
        <div className="min-h-screen bg-white text-black dark:bg-gray-900 dark:text-white">
          {/* Optional custom header with toggle */}
          <header className="p-4 bg-blue-500 dark:bg-blue-600 flex justify-between items-center">
            <h1 className="text-white text-lg font-bold">AI Tools Hub</h1>
            <ThemeToggle />
          </header>

          {/* Your App Header (optional if above header replaces it) */}
          <Header />

          <main className="container mx-auto p-4">
            <Switch>
              <Route exact path="/">
                <Hero />
                <ToolsSection openModal={openModal} />
              </Route>
              <Route path="/login" component={Login} />
              <Route path="/signup" component={Signup} />
              <Route path="/about" component={About} />
              <Route path="/contact" component={Contact} />
              <Route path="/reset-password" component={ResetPassword} />
            </Switch>
          </main>

          <Footer />

          {isModalOpen && <ComingSoonModal closeModal={closeModal} />}
        </div>
      </Router>
    </ThemeProvider>
  );
}

export default App;
